import axios from 'axios'
import { API_MARKETPLACE, API_CYBALL, NETWORK } from 'config/constant'
import { TRAIT_TYPE } from '../store/reducer'

// eslint-disable-next-line no-undef

export const useApi = () => {
  const apiUrl = `${API_MARKETPLACE}/market`

  const getTokensNft = async (filters, account) => {
    try {
      const {
        page: { pageSize, curPage },
        class: classInfo,
        id,
        trait: { type, traitFilteredList },
      } = filters
      const typeIds = classInfo.map((classItem) => classItem.id)
      const traitIds = traitFilteredList.map((classItem) => classItem.id)
      const res = await axios.get(`${apiUrl}/cyblocs?network=tomochain`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params:
          account === null
            ? {
                pageNum: curPage,
                pageSize,
                cyblocId: id,
                type: typeIds,

                ...(type === TRAIT_TYPE.NAME ? { traitType: traitIds } : { traitId: traitIds }),
              }
            : {
                pageNum: curPage,
                pageSize,
                cyblocId: id,
                type: typeIds,
                sellerAddress: account,
                ...(type === TRAIT_TYPE.NAME ? { traitType: traitIds } : { traitId: traitIds }),
              },
      })
      return res.data
    } catch (error) {
      console.log(error, 'error ?')
      return null
    }
  }

  const getCyblocList = async (account) => {
    try {
      const res = await axios.get(`${API_CYBALL}/NFTsOf`, {
        params: {
          address: account,
        },
      })

      return res.data
    } catch (error) {
      console.log(error, 'Fail to fetch cybloc list')
      return null
    }
  }

  const getFilterConfig = async () => {
    try {
      const calls = []

      const apiList = [
        `${API_MARKETPLACE}/master/cybloc/traits`,
        `${API_MARKETPLACE}/master/cybloc/types`,
        `${API_MARKETPLACE}/master/market`,
      ]

      apiList.forEach((apiLink) => {
        calls.push(axios.get(apiLink))
      })

      const result = await Promise.all(calls)
      if (result && result.length !== 0) {
        return result.map((r) => r.data)
      }

      return null
    } catch (error) {
      console.log(error, 'Fail to fetch Config')
      return null
    }
  }

  const getAccountEvents = async (account, pageNum, pageSize) => {
    try {
      const res = await axios.get(`${API_MARKETPLACE}/market/user/events/${account}`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          network: NETWORK,
          pageNum: pageNum,
          pageSize: pageSize,
        },
      })
      console.log(res, 'ressss')
      return res.data
    } catch (error) {
      console.log(error, 'Fail to fetch account events')
      return null
    }
  }
  return {
    getTokensNft,
    getFilterConfig,
    getCyblocList,
    getAccountEvents,
  }
}
