import React, { useEffect, useState, useContext, useRef } from 'react'
import { useSelector } from 'react-redux'
import { useWeb3React } from '@web3-react/core'
import queryString from 'query-string'
import { useHistory, useRouteMatch } from 'react-router-dom'
import { useQueryParam, StringParam } from 'use-query-params'
import styled from 'styled-components'
import Box from 'components/Box'
import Flex from 'components/Flex'

import Pagination from 'components/Pagination'

import NFTsGrid from 'components/NFTsGrid'
import ListFilter from './components/ListFilter'

import Tabs from './components/Tabs'
import Activity from './Activity'

import { ProfileContext } from './context/ProfileContext'
import {
  selectNftTokens,
  setFetchingSuccess,
  setFetching,
  setConfig,
  selectConfig,
  selectIsLoading,
  selectPayTokenConfig,
  selectCurrentPageNumber,
  selectTotalItems,
  selectPageSzie,
  setPageNumber,
} from './store/reducer'

import { useApi } from './api'

import LeftFilter from './components/LeftFilter'

import ExploreFilterHeader from './components/ExploreFilterHeader'

const Wrapper = styled(Box)`
  min-height: inherit;
  margin: 0 auto;
  max-width: 1400px;
  position: relative;

  margin-top: 30px;

  @media (max-width: 1000px) {
    max-width: 90%;
  }
  @media (max-width: 576px) {
    max-width: 100%;
  }
`
const Container = styled(Flex)`
  margin-top: 30px;
`

const Body = styled(Box)`
  position: relative;
  width: 100%;
  align-items: flex-start;
`

const Header = styled(Flex)`
  position: relative;
`

const ExploreAll = styled(Box)`
  overflow-y: visible;
  padding: 5px;
`
const Profile = () => {
  const { getTokensNft, getFilterConfig } = useApi()
  const { account } = useWeb3React()

  const { state, profileDispatch } = useContext(ProfileContext)
  const { filters } = state
  const currentPageNumber = useSelector(() => selectCurrentPageNumber(state))
  const totalItems = useSelector(() => selectTotalItems(state))
  const pageSize = useSelector(() => selectPageSzie(state))
  const { tokens, fetching } = useSelector(() => selectNftTokens(state))

  const { path } = useRouteMatch()
  const [tabQuery = 'collected', setTabQuery] = useQueryParam('tab', StringParam)
  const history = useHistory()

  const fetchNFTs = async (_filters) => {
    profileDispatch(setFetching(true))
    const data = await getTokensNft(_filters, tabQuery === 'created' ? account : null)
    if (data) {
      profileDispatch(setFetchingSuccess(data))
    }
  }

  useEffect(() => {
    window.scrollTo(0, 0)
    fetchNFTs(filters)
  }, [filters.class, filters.trait, currentPageNumber, filters.id, tabQuery])

  useEffect(() => {
    const fetchData = async () => {
      const result = await getFilterConfig()
      if (result && result.length !== 0) {
        profileDispatch(setConfig(result))
      }
    }
    fetchData()
  }, [])

  const onNavigateToTab = (tab) => {
    setTabQuery(tab)
    history.push(`${path}/?tab=${tab}`)
  }

  const onNavigateToOpenNFTDetail = (item) => {
    history.push(`${path}/${item.cyblocId}`)
  }

  return (
    <Wrapper>
      <Header>
        <Tabs tabs={['Collected', 'Created', 'Activity']} tabQuery={tabQuery} onClick={onNavigateToTab} />
        {tabQuery === 'activity' ? <></> : <ExploreFilterHeader />}
      </Header>
      {tabQuery === 'activity' ? (
        <Activity />
      ) : (
        <Container>
          <LeftFilter />
          <Body ml="30px">
            <ExploreAll mb="60px">
              <ListFilter />
              <NFTsGrid
                mt="30px"
                items={tokens}
                loading={fetching}
                numPerRow={null}
                category={[]}
                onClickItem={onNavigateToOpenNFTDetail}
              />
            </ExploreAll>
            {!fetching && (
              <Pagination
                currentPage={currentPageNumber + 1}
                totalCount={totalItems}
                pageSize={pageSize}
                onPageChange={(page) => profileDispatch(setPageNumber(page - 1))}
              />
            )}
          </Body>
        </Container>
      )}
    </Wrapper>
  )
}

export default Profile
