/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import get from 'lodash/get'

export const TRAIT_TYPE = {
  ID: 1,
  NAME: 0,
}

export const initialState = {
  filters: {
    type: 1,
    page: {
      curOffset: null,
      curPage: 0,
      numPages: null,
      pageSize: 16,
      total: null,
    },
    searchText: null,
    class: [],
    trait: {
      type: null,
      traitFilteredList: [],
    },
    skill: {
      shooting: null,
      crossing: null,
      passing: null,
      tackling: null,
      dribbling: null,
      physical: null,
    },
    mentoring: null,
    country: null,
    color: null,
  },
  nftTokens: {
    fetching: false,
    tokens: [],
    curPage: 0,
    curOffset: 0,
    total: 0,
  },
  isLoading: true,
  config: {
    traits: {},
    types: {},
    payToken: {},
  },
}

export const profileSlice = createSlice({
  name: 'profile',
  initialState,
  reducers: {
    setFilters: (state, action) => {
      state.filters = action.payload
    },
    setSearchText: (state, action) => {
      state.filters.searchText = action.payload
    },
    resetSearchText: (state, _) => {
      state.filters.searchText = null
    },
    resetFilters: (state, _) => {
      state.filters = initialState.filters
    },
    // nfttokens
    setFetching: (state, action) => {
      state.nftTokens.fetching = action.payload
    },
    setFetchingSuccess: (state, action) => {
      const { results, page } = action.payload
      state.nftTokens.fetching = false
      state.nftTokens.tokens = results
      state.filters.page = page
    },
    setAppendingSuccess: (state, action) => {
      const { results, page } = action.payload
      state.nftTokens.fetching = false
      state.nftTokens.tokens = [...state.nftTokens.tokens, ...results]
      state.filters.page = page
    },
    setFetchingFailed: (state, _) => {
      state.nftTokens.fetching = false
    },
    setIsLoading: (state, action) => {
      state.isLoading = action.payload
    },
    setPageNumber: (state, action) => {
      state.filters.page.curPage = action.payload
    },
    setConfig: (state, action) => {
      const data = action.payload
      const [traits, types, payToken] = data
      state.config.traits = traits
      state.config.types = types
      state.config.payToken = payToken
      state.isLoading = false
    },
    resetNftTokens: (state, _) => {
      state.nftTokens = initialState.nftTokens
    },
  },
})

export const { reducer } = profileSlice
export const {
  setFilters,
  resetFilters,
  setFetching,
  setFetchingSuccess,
  setFetchingFailed,
  resetNftTokens,
  setConfig,
  setPageNumber,
  setAppendingSuccess,
} = profileSlice.actions
export const selectIsLoading = (state) => get(state, 'isLoading', false)
export const selectFetching = (state) => get(state, 'fetching', null)
export const selectFilters = (state) => get(state, 'filters', {})
export const selectSearchText = (state) => get(state, 'filters.searchText', null)
export const selectType = (state) => get(state, 'filters.type', null)
export const selectClassFilter = (state) => get(state, 'filters.class', null)
export const selectTraitFilter = (state) => get(state, 'filters.trait.traitFilteredList', null)
export const selectTraitType = (state) => get(state, 'filters.trait.type', null)
export const selectNftTokens = (state) => get(state, 'nftTokens', null)
export const selectConfig = (state) => get(state, 'config', {})
export const selectTraitsConfig = (state) => get(selectConfig(state), 'traits', {})
export const selectTypesConfig = (state) => get(selectConfig(state), 'types', {})
export const selectPayTokenConfig = (state) => get(selectConfig(state), 'payToken', {})
export const selectCurrentPageNumber = (state) => get(state, 'filters.page.curPage', 0)
export const selectTotalItems = (state) => get(state, 'filters.page.total', 0)
export const selectPageSzie = (state) => get(state, 'filters.page.pageSize', 0)
