import React, { useContext, useCallback } from 'react'
import Text from 'components/Text'

import styled from 'styled-components'
import Flex from 'components/Flex'

import TabButton, { TAB_BUTTON_TYPE } from './TabButton'

const Wrapper = styled(Flex)`
  min-width: 400px;
  position: relative;
  height: 39px;
`

const Title = styled(Text)`
  margin: auto 17px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;

  align-items: center;
  color: #e0e0e0;
  z-index: 99;
`
const Tabs = ({ tabs = [], tabQuery, onClick }) => {
  return (
    <Wrapper>
      {tabs.map((el, idx) => (
        <TabButton
          type={
            idx === 0 ? TAB_BUTTON_TYPE.left : idx === tabs.length - 1 ? TAB_BUTTON_TYPE.right : TAB_BUTTON_TYPE.center
          }
          isActive={el.toLowerCase() === tabQuery}
          onClick={() => {
            onClick(el.toLowerCase())
          }}
        >
          <Title>{el}</Title>
        </TabButton>
      ))}
    </Wrapper>
  )
}

export default Tabs
