import React, { useContext, useEffect, useState, useMemo } from 'react'
import Skeleton from 'react-loading-skeleton'
import { useSelector } from 'react-redux'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import styled from 'styled-components'

import { Collapse } from 'react-collapse'
import {
  selectFilters,
  selectFetching,
  resetFilters,
  selectNftTokens,
  selectClassFilter,
  selectTraitFilter,
  setFilters,
  selectTotalItems,
} from 'pages/Profile/store/reducer'
import { ProfileContext } from 'pages/Profile/context/ProfileContext'

import TabButton, { TAB_BUTTON_TYPE } from '../Tabs/TabButton'

const Wrapper = styled(Flex)`
  flex-direction: column;
  // flex-wrap: wrap;
  position: relative;
  width: 100%;
`
const ResultsText = styled(Text)`
  min-width: 126px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  margin-right: 14px;
  color: #e0e0e0;
`

const TopFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  align-items: center;

  -moz-transition: height 1s ease;
  -webkit-transition: height 1s ease;
  -o-transition: height 1s ease;
  transition: height 1s ease;
  width: 100%;
`
const ListsFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  transition: height 300ms;
  width: 100%;
`

const Title = styled(Text)`
  margin: auto 15px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  // line-height: 23px;
  /* identical to box height */
  align-items: center;
  color: #e0e0e0;
  z-index: 99;
`
const CloseImage = styled.img`
  width: 39px;
  height: 39px;
  padding: 10px 20px 10px 0px;
  z-index: 99;
`
const ButtonText = styled.button`
  margin-top: 10px;
  height: 39px;
  color: #e0e0e0;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`
const ButtonClear = styled(Title)`
  color: #b499ff;
`
const Type = {
  ClassFilter: 1,
  TraitFillter: 2,
}
const ExploreFilterHeader = () => {
  const { state, profileDispatch } = useContext(ProfileContext)
  const loading = useSelector(() => selectFetching(state))
  const tokens = useSelector(() => selectNftTokens(state))
  const classFilter = useSelector(() => selectClassFilter(state))
  const traitFilter = useSelector(() => selectTraitFilter(state))
  const totalItems = useSelector(() => selectTotalItems(state))
  const filters = useSelector(() => selectFilters(state))
  const [mapListFilter, setMapListFilter] = useState([])

  useEffect(() => {
    const class_filter = classFilter.map((el) => {
      return { type: Type.ClassFilter, id: el.id, name: el.name, timestamp: el.timestamp }
    })

    const trait_filter = traitFilter.map((el) => {
      return { type: Type.TraitFillter, id: el.id, name: el.name, timestamp: el.timestamp }
    })
    const n_ListFilter = class_filter.concat(trait_filter)
    setMapListFilter(n_ListFilter.sort((a, b) => a.timestamp - b.timestamp))
  }, [filters])

  const onRemoveFilteredItem = (item) => {
    const { type } = item
    if (type === Type.ClassFilter) {
      const newClassFilter = classFilter.filter((i) => item.name !== i.name)
      profileDispatch(
        setFilters({
          ...filters,
          class: newClassFilter,
          page: {
            ...filters.page,
            curPage: 0,
          },
        }),
      )
    }

    if (type === Type.TraitFillter) {
      const newTraitFilter = traitFilter.filter((i) => item.name !== i.name)

      profileDispatch(
        setFilters({
          ...filters,
          trait: {
            ...filters.trait,
            traitFilteredList: newTraitFilter,
          },
          page: {
            ...filters.page,
            curPage: 0,
          },
        }),
      )
    }

    const filteredList = mapListFilter.filter((_item) => _item.name !== item.name)
    setMapListFilter(filteredList)
  }
  return (
    <Collapse isOpened>
      <ListsFilter>
        {mapListFilter.map((el) => (
          <TabButton mr="8px" mt="10px" type={TAB_BUTTON_TYPE.left} isActive>
            <Title>
              {el.type === Type.ClassFilter ? 'Class:' : 'Trait:'} {el.name}
            </Title>
            <CloseImage src="images/X.png" onClick={() => onRemoveFilteredItem(el)} />
          </TabButton>
        ))}
        {mapListFilter.length > 0 ? (
          <ButtonText
            onClick={() => {
              profileDispatch(resetFilters())
            }}
          >
            <ButtonClear color="#B499FF" mt="30px">
              Clear all
            </ButtonClear>
          </ButtonText>
        ) : (
          <></>
        )}
      </ListsFilter>
    </Collapse>
  )
}

export default ExploreFilterHeader
